//create new function component for details page
import React from 'react';
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Divider, Progress } from 'rsuite';
import ArrowUpIcon from '@rsuite/icons/ArrowUp';
import ArrowDownIcon from '@rsuite/icons/ArrowDown';
import TradingViewWidget, { Themes } from 'react-tradingview-widget';

const Details = (props) => {

    //fetch stock id from url 
    const { stockName } = useParams();
    const [stockResult, setStockResult] = React.useState([]);

    useEffect(() => {
        if (props.result.length > 0) {
            const stockResult = props.result.find((item) => item.stock_id === stockName);
            console.log("stockResult", stockResult);
            setStockResult(stockResult);
        }
    }, [props.isRefresh])

    return (
        <>
            <h3 style={{ color: "#000080" }}>Detail Analysis of - {stockName} ({stockResult.stock_name})</h3>
            <Divider />
            <div className="row">
                <div className='col-sm-6'>
                    <div class="card">
                        <div class="card-body">
                            Total Events Count : {stockResult && stockResult.totalEvents ? stockResult.totalEvents : 0}
                            <Divider />
                            <div className='row'>
                                <div className="col-md-6">
                                    <div className="card border-danger mt-2">
                                        <div className="card-body">
                                            <h6 className="card-title">Total Negative </h6>
                                            <Divider />
                                            <b>Counts - </b>
                                            <b className="card-text text-danger">
                                                {stockResult && stockResult.totalNegativeCount ? stockResult.totalNegativeCount : 0}
                                            </b>
                                            <Divider />
                                            <Progress.Line percent={stockResult && stockResult.totalNegativeCount ? (stockResult.totalNegativeCount / stockResult.totalEvents * 100).toFixed(2) : 0} strokeColor="#ff4d4f" status="active" />

                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="card border-success mt-2">
                                        <div className="card-body">
                                            <h6 className="card-title">Total Positive </h6>
                                            <Divider />
                                            <b>Counts - </b>
                                            <b className="card-text text-success">{stockResult && stockResult.totalPositiveCount ? stockResult.totalPositiveCount : 0}</b>
                                            <Divider />
                                            <Progress.Line percent={stockResult && stockResult.totalPositiveCount ? (stockResult.totalPositiveCount / stockResult.totalEvents * 100).toFixed(2) : 0} strokeColor="#52c41a" status="active" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='col-sm-6'>
                    <div class="card">
                        <div class="card-body">
                            Probability of Up-move and Down-move
                            <Divider />
                            <div className='row'>
                                <div className="col-md-6">
                                    <div className="card border-success mt-2">
                                        <div className="card-body">
                                            <span style={{ color: '#52c41a' }}>
                                                <b>{stockResult && stockResult.probabilityOfUpMove ? stockResult.probabilityOfUpMove.toFixed(2) : 0}</b> <ArrowUpIcon></ArrowUpIcon>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="card border-danger mt-2">
                                        <div className="card-body">
                                            <span style={{ color: "#dc3545" }}>
                                                <b> {stockResult && stockResult.probabilityOfDownMove ? stockResult.probabilityOfDownMove.toFixed(2) : 0}</b> <ArrowDownIcon></ArrowDownIcon> 
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6"></div>
                    <div className="row">
                        <div className="col-md-4 pt-2">
                            <div class="card">
                                <div class="card-body">
                                    Standard Deviation
                                    <Divider />
                                    <b>{stockResult && stockResult.standardDeviationRisk ? stockResult.standardDeviationRisk.toFixed(2) : 0} </b>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4 pt-2">
                            <div class="card">
                                <div class="card-body">
                                    Average Return
                                    <Divider />
                                    <b>{stockResult && stockResult.avarageReturn? stockResult.avarageReturn.toFixed(2):0} </b>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-4 pt-2">
                            <div class="card">
                                <div class="card-body">
                                    STDR / Average
                                    <Divider />
                                    <b> {stockResult && stockResult.standardAverageReturn ? stockResult.standardAverageReturn.toFixed(2) : 0} </b>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            <Divider />
            {stockResult && stockResult.startEndRatio ? stockResult.startEndRatio.map(obj => {
                return (
                    <div className="card">
                        <h5 className="card-header">
                            {obj.startDate.key.split("-").reverse().join("-") + " (Day Close : "+obj.startDate.data["close"]+")"+ " ----- " + obj.endDate.key.split("-").reverse().join("-") + " (Day Close : "+obj.endDate.data["close"]+")"
                        }</h5>
                        <div className="card-body">
                            <h5 className="card-title">Year Wise Ratio : {obj.ratio.toFixed(2)}</h5>
                            <p className="card-text">You can check more details in below chart</p>
                            {/* create a tradingView widget for given stockname and start and end data and mark date and price range by enabling side toolbar */}
                            <TradingViewWidget
                                symbol={"BSE:"+stockName}
                                interval="D"
                                timezone="Asia/Kolkata"
                                theme="light"
                                style="1"
                                locale="in"
                                toolbar_bg="#f1f3f6"
                                enable_publishing={false}
                                hide_top_toolbar={false}
                                allow_symbol_change={true}
                                details={true}
                                container_id="tradingview_1b1f3"
                                withdateranges={true}
                                hide_side_toolbar={false}
                                save_image={false}
                                height="500px" 
                                />



                        </div>
                    </div>
                )
            }) : null
            }

            <Divider />
        </>
    )
}

export default Details;
